@font-face {
  font-family: 'sweetSansPro';
  src: local('SweetSansProBold'), url('./fonts/sweet-sans-pro-cufonfonts-webfont/SweetSansProBold.woff') format('woff');
  src: local('SweetSansProBoldItalic'), url('./fonts/sweet-sans-pro-cufonfonts-webfont/SweetSansProBoldItalic.woff') format('woff');
  src: local('SweetSansProExLight'), url('./fonts/sweet-sans-pro-cufonfonts-webfont/SweetSansProExLight.woff') format('woff');
  src: local('SweetSansProMedium'), url('./fonts/sweet-sans-pro-cufonfonts-webfont/SweetSansProMedium.woff') format('woff');
  src: local('SweetSansProRegular'), url('./fonts/sweet-sans-pro-cufonfonts-webfont/SweetSansProRegular.woff') format('woff');
  src: local('SweetSansProMedium'), url('./fonts/sweet-sans-pro-cufonfonts-webfont/SweetSansProMedium.woff') format('woff');
}

@tailwind base;
@tailwind components;
@tailwind utilities;


body {
  margin: 0;
  font-family: 'sweetSansPro';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@keyframes rotate {
  0%{
    transform: rotate(0);
  }
  100%{
    transform: rotate(360deg);
  }
}
.loader{
  width: 100%;
  height: 100%;
  background: #f24d5d4b;
  display: flex;
  position: absolute;
  z-index: 666;
  justify-content: center;
  align-items: center;
}
.loader-circle{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 2px solid #F24D5D;
  border-bottom-color: transparent;
  animation-name: rotate;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}
.ant-pagination-item{
  border-radius: 50% !important;
}
.ant-pagination-item-active{
  background-color: #F24D5D !important;
  color: #fff !important;
  border: none !important;
}
.ant-pagination-item-active a{
  color: #fff !important;
}
.icon-search-card{
  box-shadow: 0 0px 20px #0000004D;
}

