.custon_checkbox{
  position: relative;
  


}
.custon_checkbox::before{
    content: '';
    width: 22px;
    height: 22px;
    border: 1px solid #707070;
    display: block;
    position: absolute;
    top: -2px;
    left: -4px;
    border-radius: 50%;
    cursor: pointer;
    background: #fff;
}
.custon_checkbox::after{
  content: '';
  width: 16px;
  height: 16px;
  background: #F24D5D;
  display: block;
  position: absolute;
  top: 1px;
  left: -1px;
  border-radius: 50%;
  opacity: 0;
  transition: .4s ease-in-out;
  
}
.custon_checkbox:checked::after{
  opacity: 1;
}
