.img-card{
  overflow: hidden;
}
.img-card img{
  transition: .4s ease-in-out;
}
.img-card > div:hover + img{
  transform: scale(1.1);
}
#pro_conpleted_btn{
  background: #E2E2E2;
  border-color: #DBF9FB;
}
.custom-range{
  width: 100%;
	position: absolute;
	top: 50%;
	z-index: 3;
  transform: translateY(-50%);
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  height: 4px;
  opacity: 0;
	margin: 0;
}
.custom-range::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 100px;
  height: 100px;
  cursor: pointer;
	border-radius: 50%;
	opacity: 0;
}
.custom-range::-moz-range-thumb {
  width: 14vmin;
  height: 14vmin;
  cursor: pointer;
	border-radius: 50%;
	opacity: 0;
  background-color: red;
}
.ant-pagination-options-size-changer{
  display: none !important;
}